<template>
  <div class="hello">
    
    <div class="lang">
        <button style="background : #000;color : #fff" @click="exportJSON">导出文件</button>
        <button @click="tabLang(index)" v-for="(item , index) in lang" :class="active === index ? 'active' : ''" :key="index">
          {{item.name}}
       </button>
       <button style="background : green;" @click="save">保存</button>
       <button style="background : green; width : 200px;" @click="into">倒入上次保存的文件</button>
    </div>
    <!-- 中文 -->
   <div class="wrap" v-if="!active">
        <div class="body">
        <li v-for="(item , index) in data.chinese" :key="index">
          <span>中文 {{index+1}} : </span>
          <textarea placeholder="请输入中文" v-model="data.chinese[index].txt" />
          <button @click="delate('cha' , index)">删除</button>
      </li>
      </div>
      <div class="option"> <textarea placeholder="请输入中文" v-model="data.valueCha" style="width:500px;height:80px;"/><button @click="add('cha')">添加</button></div>
   </div>
    <!-- 英文 -->
   <div class="wrap"  v-if="active === 1">
        <div class="body">
        <li v-for="(item , index) in data.english" :key="index">
          <span>英文 {{index+1}} : </span>
          <textarea placeholder="请输入英文" v-model="data.english[index].txt" />
          <button @click="delate('eng' , index)">删除</button>
      </li>
      </div>
      <div class="option"> <textarea v-model="data.valueCha" placeholder="请输入英文" style="width:500px;height:80px;"/><button @click="add('eng')">添加</button></div>
   </div>
  <!-- 阿拉伯 -->

  <div class="wrap"  v-if="active === 2">
        <div class="body">
        <li v-for="(item , index) in data.alb" :key="index">
          <span>阿语 {{index+1}} : </span>
          <textarea placeholder="请输入阿文" v-model="data.alb[index].txt" />
          <button @click="delate('alb' , index)">删除</button>
      </li>
      </div>
      <div class="option"> <textarea v-model="data.valueCha" placeholder="请输入阿文" style="width:500px;height:80px;"/><button @click="add('alb')">添加</button></div>
   </div>

  <!-- 韩语 -->
  <div class="wrap"  v-if="active === 3">
        <div class="body">
          <li v-for="(item , index) in data.han" :key="index">
        <span>韩语 {{index+1}} : </span>
          <textarea placeholder="请输入韩文" v-model="data.han[index].txt" />
          <button @click="delate('han' , index)">删除</button>
         </li>
      </div>
      <div class="option"> <textarea v-model="data.valueCha" placeholder="请输入韩文" style="width:500px;height:80px;"/><button @click="add('han')">添加</button></div>
   </div>

   <!-- 展示 -->
   <div class="wrap" v-if="active===4">
       <div class="top">
           <li v-for="(item , index) in lang" v-show="index != 4" :key="index" >
               <span>{{item.name}}</span>
           </li>
       </div>
       <div class="main">
           <li>
               <div v-for="(item , index) in data.chinese" :key="index">
                  <textarea placeholder="请输入中文" v-model="data.chinese[index].txt" />
               </div>
           </li>
           <li>
               <div v-for="(item , index) in data.english" :key="index">
                  <textarea placeholder="请输入英文" v-model="data.english[index].txt" />
               </div>
           </li>
           <li>
               <div v-for="(item , index) in data.alb" :key="index">
                  <textarea placeholder="请输入阿文" v-model="data.alb[index].txt" />
               </div>
           </li>
           <li>
               <div v-for="(item , index) in data.han" :key="index">
                  <textarea placeholder="请输入韩文" v-model="data.han[index].txt" />
               </div>
           </li>
       </div>
   </div>
  </div>
</template>

<script>
import FileSaver from 'file-saver'
export default {
  name: 'HelloWorld',
  data () {
    return {
      data : {
          chinese : [

          ],
          english : [
            
          ],
          alb : [

          ],
          han : [

          ],
          valueCha :''
      },
      lang : [
        {
          name : '中文',
        },
        {
          name : '英文',
        },
        {
          name : '阿文',
        },
        {
          name : '韩文',
        },
        {
          name : '显示'
        }
      ],
      active : 0
    }
  },
  beforeCreate () {
      let login = sessionStorage.getItem('login')
      if(!login) {
          this.$router.push('/')
          return;
      }
  },
  methods : {
    //倒入上次保存的文件
    into () {
      let intoSave = JSON.parse(localStorage.getItem('ListY'))
      if(intoSave) {
        this.data = intoSave
        alert('倒入成功')
        return;
      }else {
        alert('不好意思，没找到～～～')
        return;
      }
    },
    //本地保存
    save () {
      if(confirm('此次保存是本地存储，不要做清除缓存操作，切换浏览器后会失效，换电脑也是哦，要专一一个浏览器～～')) {
        localStorage.setItem('ListY' , JSON.stringify(this.data))
        alert('本地保存成功～～～')
        return;
      }else {
        alert('不存拉倒，记得自己备份～～～')
        return;
      }
    },
    //删除列表某一项
    delate (type , ind) {
      if(!confirm('如果你要删除这一行请把所有语言的这一行都删除，切记！！！！！')) {
        return;
      }
      if(type === 'cha') {
        this.data.chinese.splice(ind, 1);
      }else if (type === 'eng') {
        this.data.english.splice(ind, 1);
      }else if (type === 'alb') {
        this.data.alb.splice(ind, 1);
      }else if (type === 'han') {
        this.data.han.splice(ind, 1);
      }
    },
    exportJSON () {
          //中文转格式
          let cha = this.data.chinese
          let eng = this.data.english
          let alb = this.data.alb
          let han = this.data.han
          let newCha = {}
          cha.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newCha = Object.assign({},newCha,{[keyname] : item.txt})
          })

          //英文转格式
          let newEng = {}
          eng.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newEng = Object.assign({},newEng,{[keyname] : item.txt})
          })

          //阿语转格式
          let newAlb = {}
          alb.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newAlb = Object.assign({},newEng,{[keyname] : item.txt})
          })

          // 韩语转格式
          let out =[]
          let newHan = {}
          han.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newHan = Object.assign({},newEng,{[keyname] : item.txt})
          })
          out.push({'zw' : newCha})
          out.push({'yw' : newEng})
          out.push({'alb' : newAlb})
          out.push({'Han' : newHan})
          // console.log(out)
          // 将json转换成字符串
          const data = JSON.stringify(out)
          const blob = new Blob([data], {type: ''})
          FileSaver.saveAs(blob, 'data.json')
      },

      //添加语言
      add (val) {
        if(!this.data.valueCha) {
          alert('不能不输入东西～～～')
          return;
        }
        let defa = this.data.chinese
        if(val==='cha') {
            defa = this.data.chinese
            this.data.english.push({txt : ''})
            this.data.alb.push({txt : ''})
            this.data.han.push({txt : ''})
        }else if(val === 'eng'){
            defa = this.data.english
            this.data.chinese.push({txt : ''})
            this.data.alb.push({txt : ''})
            this.data.han.push({txt : ''})
        }else if(val === 'alb'){
            defa = this.data.alb
            this.data.english.push({txt : ''})
            this.data.chinese.push({txt : ''})
            this.data.han.push({txt : ''})
        }else if(val === 'han'){
            defa = this.data.han
            this.data.english.push({txt : ''})
            this.data.chinese.push({txt : ''})
            this.data.alb.push({txt : ''})
        }
        defa.push({
          txt : this.data.valueCha
        })
        this.data.valueCha = ''
        if(val==='cha') {
             this.data.chinese = defa
        }else if(val === 'eng'){
             this.data.english = defa
        }else if(val === 'alb'){
             this.data.alb = defa
        }else if(val === 'han'){
             this.data.han = defa
        }
      },
      tabLang (ind) {
        this.active = ind
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html , body , #app  {
    height: 100%;
    width: 100%;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.body {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
.body li {
  /* margin-top: 10px; */
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ccc;
}
.body li button {
  width: 60px;
  height: 20px;
  background: red;
  cursor: pointer;
  border: none;
  color: #fff;
}
.body li textarea {
  border: none;
  padding: 2px;
  width: 100%;
  height: 50px;;
}
.body li span {
  margin-right: 20px;
  width: 80px;
  white-space: nowrap;
}
.option {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
.option
button {
  height: 30px;
  width: 100px;
  background: lightblue;
  cursor: pointer;
}
.lang button {
  height: 30px;
  width: 80px;
  background: lightcoral;
  margin-left: 10px;
  border: none;
  color: #fff;
  cursor: pointer;
}
.lang button.active {
  background: lime;
  color: #000;
}
.wrap .top {
    width: 100%;
    display: flex;
    text-align: center;
    height: 30px;
    border: 1px solid #ccc;
    margin-top: 10px;
    line-height: 30px;
}
.wrap .main {
    display: flex;
}
.wrap .main li {
    flex: 1;
    height: 100px;
}
.wrap .main li div {
    height: 100px;
    margin-top: 10px;
}
</style>



